import React from "react";
import s from "./NavButtonOpenModal.module.css"
import { useDispatch } from "react-redux";
import { changeIsModalOpen } from "../../bll/headerReducer";

export const NavButtonOpenModal = () => {
  let dispatch=useDispatch()
  const setIsModalOpen=()=>{
    dispatch(changeIsModalOpen(true))
  }
  return (
    <>
      <div className={s.navSendMe}>
        <div
          className={s.sendMeMessage}
          onClick={setIsModalOpen}
        >
          Написать мне
        </div>
      </div>
    </>
  );
};
