import { createSlice } from "@reduxjs/toolkit"

const initialState={
    nav:[
        {id:0,name:"О себе",href:"section1"},
        {id:1,name:"Скилл",href:"section2"},
        {id:2,name:"Мои работы",href:"section3"},
    ],
    offsetContentTop:[],
    scrollPoints:["300"],
    isShowBurgerMenu:false,
    isModalOpen:false,
}

const headerReducer=createSlice({
    name:"header",
    initialState,
    reducers:{
        changeBurgerMenu:(state)=>{
            state.isShowBurgerMenu=!state.isShowBurgerMenu
        },
        addOffsetContentTop:(state,actions)=>{
            state.offsetContentTop=actions.payload
        },
        changeIsModalOpen:(state,action)=>{
            state.isModalOpen=action.payload
        }
    }
})

export const {changeBurgerMenu,addOffsetContentTop,changeIsModalOpen}=headerReducer.actions;
export default headerReducer.reducer