import react, { useEffect, useRef, useState } from "react";
import { Link, animateScroll as scroll } from "react-scroll";
import s from "./NavLinks.module.css"
import { useSelector } from "react-redux";

export const NavLinks = () => {
  let [activeButton,setActiveButton]=useState(0)
  let offsetContentTop=useSelector(state=>state.header.offsetContentTop)
  const navList=useSelector(state=>state.header.nav)
  const isShowBurgerMenu=useSelector(state=>state.header.isShowBurgerMenu)

  const handleScroll=()=>{
    const scrollPosition = window.scrollY;
    if (scrollPosition<offsetContentTop[1]) {
      setActiveButton(0)
    }else if (scrollPosition>=offsetContentTop[1] && scrollPosition<offsetContentTop[2]) {
      setActiveButton(1)
    }else{
      setActiveButton(2)
    }
  }

  useEffect(()=>{
    window.addEventListener('scroll',handleScroll)
    return ()=>{
      window.removeEventListener('scroll',handleScroll)
    }
  },[handleScroll])


  let lis=navList.map((link)=> <li key={link.id} className={`${s.navLink} ${activeButton===link.id?s.active:""} `}>
    <Link to={link.href} duration={500} smooth>{link.name}</Link>
  </li>
  )
  return (
    <ul
      className={ `${s.navLinks} ${isShowBurgerMenu&&s.activeBurger}`}>
      {lis}
      <li className={`${s.navLink} ${s.navLinkForMobile}`}><a href="##">Скачать резюме</a></li>
      <li className={`${s.navLink} ${s.navLinkForMobile}`}><a href="##">Написать мне</a></li>
    </ul>
  );
};
